import React from 'react';
import { useNavigate } from 'react-router-dom';
//    
const MainNavBtn = ({ className }) => {
  const navigate = useNavigate();
  return (
    <ul className={`home-nav ${className}`}>
      <li>
        <a href="#" className="home-nav-list active">
          <i className="icon icon-home"></i>
          <span>Home</span>
        </a>
      </li>
      <li>
        <a
          href="#"
          className="home-nav-list"
          onClick={(e) => {
            e.preventDefault(); //    .
            navigate('/chat');
          }}
        >
          <i className="icon icon-chat"></i>
          <span>Chat</span>
        </a>
      </li>
      <li>
        <a
          href="#"
          className="home-nav-list"
          onClick={(e) => {
            e.preventDefault(); //    .
            navigate('/mypage/library');
          }}
        >
          <i className="icon icon-book"></i>
          <span>Bookmark</span>
        </a>
      </li>
      <li>
        <a
          href="#"
          className="home-nav-list"
          onClick={(e) => {
            e.preventDefault(); //    .
            navigate('/mypage');
          }}
        >
          <i className="icon icon-my"></i>
          <span>My Page</span>
        </a>
      </li>
    </ul>
  );
};

export default MainNavBtn;
