import React, { useEffect, useRef, useState } from 'react';
import axios from 'axios';
import { BasicBtn } from '../../components/button/BtnStyle';
import { IconIpt, SearchIpt } from '../../components/input/IptStyleEtc';
import { BasicIpt } from '../../components/input/IptStyle';
import EmptyList from './EmptyList';
import styles from './css/BookSearchDetailModal.module.css';
import LoadingModal from '../../components/popup/LoadingModal';

const BookSearchDetailModal = (props) => {
  const [searchText, setSearchText] = useState('');
  const [searchAlert, setSearchAlert] = useState('');
  const [searchResults, setSearchResults] = useState([]); // api 
  const [noResults, setNoResults] = useState(false);
  const searchInputRef = useRef(null);

  // 
  const [isBottom, setIsBottom] = useState(false);
  const [pageToFetch, setpageToFetch] = useState(1);

  //   
  const [isLoading, setIsLoading] = useState(false);

  //    body 
  useEffect(() => {
    document.body.style.cssText = `
          position: fixed; 
          top: -${window.scrollY}px;
          overflow-y: scroll;
          width: 100%;`;
    return () => {
      const scrollY = document.body.style.top;
      document.body.style.cssText = '';
      window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
    };
  }, []);

  //      
  useEffect(() => {
    if (searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, []);

  //  
  const closePopup = () => {
    props.closePopup();
  };

  //  
  const selectBook = (selectedBook) => {
    props.onBookSelect(selectedBook);
    props.closePopup();
  };

  //  API
  const fetchBook = async (count, start, eventType) => {
    if (searchText === '') return;
    setIsLoading(true);
    const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
    const aladinUrl = `http://www.aladin.co.kr/ttb/api/ItemSearch.aspx?ttbkey=ttb22pqpq1534001&Query=${searchText}&QueryType=Title&MaxResults=${count}&start=${start}&SearchTarget=Book&Cover=Big&output=JS&Version=20131101`;
    try {
      const response = await axios.get(proxyUrl + aladinUrl);
      if (response.status === 200) {
        setIsLoading(false);
        const res = response.data.item;
        //eventType:  (search), (scroll) 
        if (eventType === 'search') {
          if (response.data.item.length === 0) {
            setNoResults(true);
            return;
          }
          setSearchResults(res);
          setNoResults(false);
          return;
        }
        setSearchResults(searchResults.concat(res));
        setNoResults(false);
      }
    } catch (error) {
      setIsLoading(false);
      console.error('Error fetching data: ', error);
    }
  };

  const handleSearch = (e) => {
    e.preventDefault();
    //        if (  API  X)
    if (searchText === '') {
      searchInputRef.current.focus();
      setSearchAlert(
        <p className={styles['search-alert']}>
              
        </p>,
      );
      return;
    }
    setSearchAlert('');
    //  API 
    fetchBook(30, 1, 'search');
  };

  //   
  const handleSearchKeyUp = (e) => {
    if (e.key === 'Enter') {
      handleSearch(e);
    }
  };

  //  
  function isBottomFn(e) {
    if (
      e.target.scrollTop + e.target.offsetHeight >=
      e.target.children[0].offsetHeight
    ) {
      setIsBottom(true);
    } else {
      setIsBottom(false);
    }
  }
  useEffect(() => {
    if (isBottom) {
      setpageToFetch((prevPage) => {
        return prevPage + 1;
      });
    }
  }, [isBottom]);

  useEffect(() => {
    if (!isLoading) {
      fetchBook(30, pageToFetch, 'scroll');
    }
  }, [pageToFetch]);

  return (
    <div className="modal-area">
      <div className="modal-bg" />
      <div className="modal-wrap">
        {isLoading ? <LoadingModal /> : ''}
        <div className={styles['modal-title']}>
          <h2> </h2>
          <strong>   </strong>
        </div>
        <div className={styles.searchWrap}>
          <SearchIpt>
            <IconIpt>
              <BasicIpt
                type="text"
                placeholder="  ."
                value={searchText}
                onChange={(e) => setSearchText(e.target.value)}
                onKeyUp={handleSearchKeyUp}
                ref={searchInputRef}
              />
              <i class="icon icon-search" />
            </IconIpt>
            <BasicBtn onClick={(e) => handleSearch(e)}></BasicBtn>
          </SearchIpt>
          {searchAlert}
        </div>
        <h3 className="modal-title"> </h3>
        {noResults ? (
          <EmptyList text="  ." />
        ) : (
          <div
            className={styles.scrollArea}
            onScroll={(e) => {
              isBottomFn(e);
            }}
          >
            <div className="modal-content row-2">
              {searchResults.map((book, i) => (
                <div className="row-2" key={i} onClick={() => selectBook(book)}>
                  <div className="book-info-obj">
                    <img alt=" " src={book.cover} />
                  </div>
                  <div className="book-info-text">
                    <strong className="book-public">
                      <span> |</span> {book.publisher}
                    </strong>
                    <h3 className="book-title">{book.title}</h3>
                    <div>
                      <span className="tag orange"></span>
                      <strong className="book-author">{book.author}</strong>
                    </div>
                    <p className="book-content ellipsis">{book.description}</p>
                  </div>
                  <p className="end-point" />
                </div>
              ))}
            </div>
          </div>
        )}
        <BasicBtn
          round="0px"
          bgcolor="#E87C3E"
          shadow="none"
          wid="100%"
          onClick={closePopup}
        >
          
        </BasicBtn>
      </div>
    </div>
  );
};

export default BookSearchDetailModal;
